import React, { useState, useRef, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { ChromePicker } from 'react-color';

const UNSPLASH_API_KEY = 'e9aM3F15rlpymlIZz_CUMz4dWlo0pmi4Vyg9Mzq_r3c';
const UNSPLASH_API_URL = 'https://api.unsplash.com/search/photos';

function App() {
  const [promptText, setPromptText] = useState('');
  const [images, setImages] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(null);
  const [zoomedImage, setZoomedImage] = useState(null);
  const [text, setText] = useState('');
  const [showTextInput, setShowTextInput] = useState(false);
  const [backgroundColor, setBackgroundColor] = useState('#000000');
  const [showColorPicker, setShowColorPicker] = useState(false);
  const [showGrid, setShowGrid] = useState(false);
  const [gridThickness, setGridThickness] = useState(1);
  const moodboardRef = useRef(null);
  let clickTimeout = null;

  useEffect(() => {
    const handleResize = () => {
      // Trigger a re-render when the window is resized
      setImages([...images]);
    };

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, [images]);

  const generateMoodboard = async () => {
    if (promptText.trim() === '') {
      setError('Please enter a prompt before starting your journey.');
      return;
    }
    setIsLoading(true);
    setError(null);

    try {
      const response = await fetch(`${UNSPLASH_API_URL}?query=${promptText}&per_page=9`, {
        headers: {
          'Authorization': `Client-ID ${UNSPLASH_API_KEY}`
        }
      });

      if (!response.ok) {
        throw new Error('Failed to fetch images');
      }

      const data = await response.json();
      const newImages = data.results.map((img, index) => ({
        id: Date.now() + index,
        src: img.urls.regular,
      }));

      setImages(newImages);
    } catch (err) {
      setError(`Failed to generate moodboard: ${err.message}`);
    } finally {
      setIsLoading(false);
    }
  };

  const shuffleImages = () => {
    setImages(images => [...images].sort(() => Math.random() - 0.5));
  };

  const addNewImage = async () => {
    setIsLoading(true);
    try {
      const response = await fetch(`${UNSPLASH_API_URL}?query=${promptText}&per_page=1`, {
        headers: {
          'Authorization': `Client-ID ${UNSPLASH_API_KEY}`
        }
      });

      if (!response.ok) {
        throw new Error('Failed to fetch image');
      }

      const data = await response.json();
      if (data.results.length > 0) {
        const newImage = {
          id: Date.now(),
          src: data.results[0].urls.regular,
        };
        setImages(prevImages => [...prevImages, newImage]);
      }
    } catch (err) {
      setError(`Failed to add new image: ${err.message}`);
    } finally {
      setIsLoading(false);
    }
  };

  const exportMoodboard = () => {
    console.log('Exporting moodboard...');
    // Implement your export functionality here
  };

  const BackgroundImages = () => (
    <div className="fixed inset-0 z-0 overflow-hidden">
      {[...Array(10)].map((_, i) => (
        <motion.img
          key={i}
          src={`https://picsum.photos/seed/${i}/200/200?grayscale`}
          alt=""
          className="absolute object-cover"
          style={{
            width: '10%',
            height: '10%',
            opacity: 1,
          }}
          initial={{
            x: Math.random() * window.innerWidth,
            y: Math.random() * window.innerHeight,
          }}
          animate={{
            x: Math.random() * window.innerWidth,
            y: Math.random() * window.innerHeight,
          }}
          transition={{
            duration: 60,
            repeat: Infinity,
            repeatType: 'reverse',
          }}
        />
      ))}
    </div>
  );

  const LoadingAnimation = () => (
    <motion.div 
      className="fixed inset-0 z-50 bg-black"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      exit={{ opacity: 0 }}
    >
      {[...Array(1000)].map((_, i) => (
        <motion.div
          key={i}
          className="absolute w-1 h-1 bg-white rounded-full"
          initial={{
            x: Math.random() * window.innerWidth,
            y: Math.random() * window.innerHeight,
          }}
          animate={{
            x: window.innerWidth / 2,
            y: window.innerHeight / 2,
          }}
          transition={{
            duration: Math.random() * 2 + 1,
            ease: 'easeInOut',
          }}
        />
      ))}
    </motion.div>
  );

  const Grid = () => (
    <div 
      className="absolute inset-0" 
      style={{
        backgroundImage: `
          linear-gradient(to right, rgba(255,255,255,0.1) ${gridThickness}px, transparent ${gridThickness}px),
          linear-gradient(to bottom, rgba(255,255,255,0.1) ${gridThickness}px, transparent ${gridThickness}px)
        `,
        backgroundSize: '50px 50px',
      }}
    />
  );

  return (
    <div className="relative min-h-screen text-white">
      {images.length === 0 && <BackgroundImages />}
      <AnimatePresence>
        {isLoading && <LoadingAnimation />}
      </AnimatePresence>
      <div className="relative z-10 min-h-screen">
        {images.length === 0 ? (
          <div className="flex flex-col items-center justify-center min-h-screen p-4">
            <input
              type="text"
              value={promptText}
              onChange={(e) => setPromptText(e.target.value)}
              onKeyPress={(e) => e.key === 'Enter' && generateMoodboard()}
              placeholder="Your creative journey starts here...."
              className="w-full max-w-2xl bg-gray-200 text-black text-1xl rounded-full py-2 px-6 mb-4 focus:outline-none focus:ring-2 focus:ring-black"
            />
            <button
              onClick={generateMoodboard}
              className="bg-black text-white py-2 px-4 rounded-full mt-4"
            >
              Generate Moodboard
            </button>
            {error && <p className="text-red-500 mt-2">{error}</p>}
          </div>
        ) : (
          <div className="p-4 flex justify-center items-center min-h-screen" ref={moodboardRef} style={{ backgroundColor }}>
            {showGrid && <Grid />}
            <div className="grid grid-cols-3 gap-4 max-w-5xl relative">
              {images.map((img) => (
                <motion.div
                  key={img.id}
                  className="aspect-square overflow-hidden cursor-move relative touch-none"
                  drag
                  dragMomentum={false}
                  dragConstraints={moodboardRef}
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                  onMouseDown={(e) => {
                    if (clickTimeout) clearTimeout(clickTimeout);
                    clickTimeout = setTimeout(() => {
                      clickTimeout = null;
                    }, 300);
                  }}
                  onDoubleClick={() => setZoomedImage(img)}
                >
                  <motion.img 
                    src={img.src} 
                    alt="" 
                    className="w-full h-full object-cover pointer-events-none"
                    initial={{ opacity: 0 }}
                    animate={{ opacity: 1 }}
                    transition={{ duration: 0.5 }}
                  />
                </motion.div>
              ))}
              {text && (
                <motion.div
                  className="absolute text-white text-2xl font-bold cursor-move z-10 touch-none"
                  drag
                  dragMomentum={false}
                  dragConstraints={moodboardRef}
                  initial={{ opacity: 0, scale: 0.5 }}
                  animate={{ opacity: 1, scale: 1 }}
                  style={{ top: '50%', left: '50%', x: '-50%', y: '-50%' }}
                >
                  {text}
                </motion.div>
              )}
            </div>
          </div>
        )}
      </div>
      {images.length > 0 && (
        <div className="fixed bottom-4 left-1/2 transform -translate-x-1/2 flex justify-center space-x-2 bg-white text-black rounded-full p-2 z-50 flex-wrap">
          <button onClick={() => {setImages([]); setPromptText(''); setText(''); setShowTextInput(false); setShowColorPicker(false); setShowGrid(false);}} className="px-2 py-1 rounded-full">
            Re-Prompt
          </button>
          <button onClick={shuffleImages} className="px-2 py-1 rounded-full">
            Shuffle
          </button>
          <button onClick={() => setShowColorPicker(!showColorPicker)} className="px-2 py-1 rounded-full">
            Background
          </button>
          <button onClick={() => setShowGrid(!showGrid)} className="px-2 py-1 rounded-full">
            {showGrid ? 'Hide Grid' : 'Show Grid'}
          </button>
          <button onClick={() => setShowTextInput(!showTextInput)} className="px-2 py-1 rounded-full">
            Add Text
          </button>
          <button onClick={addNewImage} className="px-2 py-1 rounded-full">
            Image
          </button>
          <button onClick={exportMoodboard} className="px-2 py-1 rounded-full">
            Export
          </button>
        </div>
      )}
      {showColorPicker && images.length > 0 && (
        <div className="fixed bottom-20 left-1/2 transform -translate-x-1/2 z-50">
          <ChromePicker color={backgroundColor} onChange={(color) => setBackgroundColor(color.hex)} />
        </div>
      )}
      {showGrid && images.length > 0 && (
        <div className="fixed bottom-20 left-1/2 transform -translate-x-1/2 z-50 bg-white p-4 rounded">
          <input 
            type="range" 
            min="1" 
            max="5" 
            value={gridThickness} 
            onChange={(e) => setGridThickness(e.target.value)} 
          />
        </div>
      )}
      {showTextInput && images.length > 0 && (
        <div className="fixed top-4 left-1/2 transform -translate-x-1/2 z-50">
          <input
            type="text"
            value={text}
            onChange={(e) => setText(e.target.value)}
            placeholder="Add text to moodboard"
            className="bg-white text-black px-4 py-2 rounded-full"
          />
        </div>
      )}
      {zoomedImage && (
        <div className="fixed inset-0 bg-black bg-opacity-75 flex justify-center items-center z-50" onClick={() => setZoomedImage(null)}>
          <img src={zoomedImage.src} alt="" className="max-w-full max-h-full" />
        </div>
      )}
    </div>
  );
}

export default App;
